import {
    mapState,
    mapMutations
} from 'vuex'

export default {
    data() {
        return {
            title: 'login',
            providerList: []
        }
    },
    computed: {
        ...mapState(['hasLogin'])
    },
    onLoad() {
        uni.getProvider({
            service: 'oauth',
            success: (result) => {
                this.providerList = result.provider.map((value) => {
                    let providerName = '';
                    switch (value) {
                        case 'weixin':
                            providerName = '微信登录'
                            break;
                        case 'qq':
                            providerName = 'QQ登录'
                            break;
                        case 'sinaweibo':
                            providerName = '新浪微博登录'
                            break;
                        case 'xiaomi':
                            providerName = '小米登录'
                            break;
                        case 'alipay':
                            providerName = '支付宝登录'
                            break;
                        case 'baidu':
                            providerName = '百度登录'
                            break;
                        case 'toutiao':
                            providerName = '头条登录'
                            break;
                        case 'apple':
                            providerName = '苹果登录'
                            break;
                    }
                    return {
                        name: providerName,
                        id: value
                    }
                });

            },
            fail: (error) => {
                console.log('获取登录通道失败', error);
            }
        });
    },
    methods: {
        ...mapMutations(['login']),
        tologin(provider) {
            uni.login({
                provider: provider.id,



                success: (res) => {
                    console.log('login success:', res);
                    // 更新保存在 store 中的登录状态
                    this.login(provider.id);
                },
                fail: (err) => {
                    console.log('login fail:', err);
                }
            });
        }
    }
}
